@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;500&family=Raleway:wght@200&family=Roboto+Condensed:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    padding :0px;
    margin:0;
}
.border-card{
    border:1px solid #93c7ba;
    box-shadow:3px 4px 5px  #93c7ba;
    width:80%;
    padding:2px;
}
.card-style{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    
}
.div-line{
    width: 95%;
    height: 2px;
    margin: 1% 0% 6% 0%;
    background-color: #c7ad9c;
}
.image-style{
    width: 90%;
    height:210px;
    border-image: 1 solid #c7ad9c;


}
.product{
    font-weight: 700;
    font-size: 23px;
}
.font{   
      font-family: 'Raleway', sans-serif;
}
.information-style{
    padding:0%  8%;
    display: flex;
    flex-direction: column;
    font-weight: 900;
    align-items: flex-start;
}
.container{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;

}
.category{ font-weight: 900;}
.price{
    font-weight: 900;
}
.discount{
    font-size: 15px;
    color: whitesmoke;
    margin-left: 4%;
    background-color: rgb(189, 5, 5);
    border-radius: 1em;
    width: 20%;
    height: 10%;
    align-content: center;
}
.choices-container{
    display: flex;
    padding-top:4%;
    justify-content: space-between;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

